@use 'variables' as v;


html{
    font-size: 62.5%;
    box-sizing: border-box;
}
*,*:before, *:after{
    box-sizing: inherit;
}

body{
    font-family: v.$fuenteSecundaria;
    font-size: 1.6rem;
    overflow-x: hidden;
    
}
p {
    color: v.$negro;
    font-size: 1.6rem;
    line-height: 2;
}

.container{
    width: 90%;
    max-width: 115rem;
    margin: 0 auto;
}

a{
    text-decoration: none;
}

img{
    max-width: 100%;
    display: block;
}

ul{
    list-style: none;
    padding: 0;
    margin: 0;
}



