@use '../../base/mixins' as m;
@use '../../base/variables' as v;

.about_container_photo{
    background-image: url(https://sarahssnacks.com/wp-content/uploads/2019/11/about-bg.jpg);
    background-position: center center;
    background-repeat:no-repeat;
    background-size:cover;
    position: relative;
    min-height: 40rem;
    @include m.phone { 
        height: 22rem;
     }
}

.about_opacity{
    position: absolute;
    background-color: black;
    opacity: 0.5;
    height: 100%;
    width: 100%;
}

.about_photo_text{
    height: 100%;
    position: relative;
    padding: 17rem 0 13rem 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin: 0 auto;
    @include m.phone { 
        padding: 9 0 4rem 0;
     }
    span{
        width: 60%;
        font-size: 7rem;
        color: white;
        font-weight: 700;
        text-transform: uppercase;
        text-align: center;
        @include m.phone { 
            width: 90%;
         }
    }

    h3{
        width: 35%;
        font-size: 2.5rem;
        color: white;
        font-weight: 300;
        @include m.phone { 
            width: 90%;
         }
    }
}


// ----------------------- section history and resume----------------------

.about_history_flex{
    
    margin-top: 8rem;
    margin-bottom: 8rem;
    display: flex;
    flex-direction: column;
    gap: 4rem;
    justify-content: center;
    align-items: flex-start;
}

.about_tittle{
    text-transform: uppercase;
    line-height: 0.7;
    color: v.$primary;
    font-size: 3.8rem;
    @include m.phone { 
        font-size: 2.5rem;
        line-height: 1;
     }
    h3{
        text-align: start;
    }
}

.about_history_p{
    width: 70%;
    @include m.phone { 
        width: 100%;
     }
    p{
        font-size: 1.8rem;
        font-weight: 300;
        line-height: 1.7;
        margin: 0 0 1.5rem 0;
        @include m.phone { 
            font-size: 1.6rem;
           
         }
    }
    p:last-child{
        margin-bottom: 0;
    }
}


