@use '../../base/mixins' as m;
@use '../../base/variables' as v;

.background_primary_container{
    background-image: url(https://sarahssnacks.com/wp-content/uploads/2017/10/teal-blue-pattern-01.png);
    background-position: center center;
    background-repeat: repeat;
    background-size: 50px;
    background-color: rgba(0,146,136,0.86);
    background-attachment: fixed;
    position: relative;
    min-height: 60rem;

    @include m.phone { 
        min-height: 45rem;
     }
}

.background_opacity{
    position: absolute;
    background-color: #ffffff;
    opacity: 0.5;
    height: 100%;
    width: 100%;
    
}

.container_information{
    top: 0;
    left: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    padding: 27rem 0 10rem 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    margin: 1px;

    @include m.phone { 
        padding: 15rem 0 12rem 0;
     }
    
}


.container_text{ 
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    h2{
        color: #ffffff;
        font-family: inherit;
        font-size: 5.6rem;
        font-weight: 300;
        margin: 0;
        line-height: 0.4;
        max-width: 100%;
    }
    h1{
        margin: 0;
        color: #ffffff;
        font-size: 11rem;
        line-height: 1;
        max-width: 100%;
    }
    h3{
        margin: 0;
        color: #ffffff;
        font-size: 2.4rem;
        line-height: 0.7;
        font-weight: 400;
        max-width: 100%;
    }

    @include m.phone { 
        max-width: 80%;
        h2{
            font-size: 2.8rem;
            line-height: 0.6;
            display: block;
        }
        h1{
            font-size: 4rem;
            display: block;
        }
        h3{
            font-size: 1.5rem;
            line-height: 1;
            display: block;
        }
     }

     @include m.tablet { 
        h2{
            font-size: 3.5rem;
            line-height: 0.6;
        }
        h1{
            font-size: 4.7rem;
        }
        h3{
            font-size: 2.2rem;
        }
      }
}

.redirect_container{
    max-width: 30rem;
    @include m.phone { 
        max-width: 80%;
     }
}

.btn_toProducts{
    
    background-color: #4e3629;
    color: #ffffff;
    padding: 1.2rem;
    font-family: 'Montserrat', sans-serif;
    font-size: 1.2rem;
    letter-spacing: 1px;
    display: flex;
    gap: 0.5rem;
    transition: all 0.5s;
    &_green{
        background-color: v.$primary;
        padding: 1.5rem;
    }
    svg {
        padding: auto;
        width: 1.2rem;
        height: 1.2rem;
    }
    &:hover{
        background-color: #814028;
    }

    
  
}



// slide


.container_slide{

    max-width: 95%;
    margin: 14rem auto 0 auto;
    display: grid;
    grid-template-columns: 3fr 2fr;
    overflow: hidden;
    @include m.tablet { 
        margin: 5rem auto 0 auto;
        grid-template-columns: 1fr;
     }

    .slider{
        overflow: hidden;
    }
}

.container-slider-grid{
    display: grid;
  column-gap: 2.3rem;
  padding-right: 4rem;
  grid-template-columns: repeat(auto-fit,minmax(150px,1fr));
  grid-template-rows: 1fr;
  @include m.tablet { 
    grid-template-columns: repeat(8, 1fr);

    
    
 }
}

.favorites_flex{
    height: 85%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    padding: 0 3rem;
    gap: 2rem;
    @include m.phone { 
        grid-row: 1/2;
        margin-bottom: 5rem;
        }

    .text_favorite{
        font-size: 4rem;
        color: v.$primary;
        text-transform: uppercase;
        font-weight: 300;
        @include m.phone { 
            font-size: 3rem;
            }
        span{
            font-weight: 900;
            display: block;
            
        }
    }

    .information_favorite{
        font-weight: 300;
        line-height: 2.7rem;
        font-size: 1.9rem;
        @include m.phone { 
            font-size: 1.6rem;
            }
    }
}

//---------------frase------------------------------------//

.phrase{ 
    margin: 5rem 0;
    background-image: url(https://sarahssnacks.com/wp-content/uploads/2019/11/testimonial-background.jpg);
    background-position: center center;
    background-size:cover;
    background-attachment: fixed;
    background-repeat: no-repeat;
    position: relative;
    @include m.phone { 
        background-attachment: scroll;
        margin: 0;
        margin-bottom: 4rem;
        }
}

.phrase_opacity{
    position: absolute;
    background-color: #000000;
    opacity: 0.5;
    height: 100%;
    width: 100%;
 
}

.phrase_grid{
    padding: 15rem 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    position: relative;
    min-height: 52rem;

    
    svg{
        height: 5rem;
        width: 5rem;
        fill: v.$primary;
        
    }

    .phrase_text{
        width: 40%;
        text-align: center;
        @include m.phone { 
            width: 80%;
            }
        span{

        width: 40%;
        font-size: 4rem;
        color: #ffffff;
        background: #333;
        text-transform: uppercase;
        @include m.phone { 
            background: none;
            font-size: 3rem;
            }
        
        span{
            margin: 0 auto;
            font-weight: 700;
            display: block;
            background: #333;
            @include m.phone { 
                background: none;
                }
        }
        }
    }
}
